$small: 449px;
$medium: 760px;
$large: 1000px;

body {
  height: 99vh;
}
.about-wrap {
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 85vh;
  width: 55vw;
  margin: 0 auto;
  box-shadow: 2px 5px 15px rgba(0, 0, 0, 0.404);
  border-radius: 10px;
  @media screen and (max-width: $small) {
    width: 90vw;
    font-size: 0.75rem;
  }
  @media screen and (min-width: $small) {
    width: 90vw;
  }
  @media screen and (min-width: $medium) {
    width: 90vw;
  }
  @media screen and (min-width: $large) {
    width: 50vw;
    height: 90vh;
  }
  h1 {
    font-size: 2rem;
  }
  .image {
    background-image: url("https://sun9-85.userapi.com/impg/AnIREoZTEDkhEeDgFSelhJ0ibetybY1FzW7mTg/-coD18OZQ-U.jpg?size=1721x2160&quality=95&sign=907a9728ffab55f4dd39476a3e477120&type=album");
    background-size: cover;
    background-position: bottom;
    width: 50vw;
    height: 50vh;
    border-radius: 50%;
    @media screen and (min-width: $medium) {
      width: 550px;
      height: 550px;
    }
    @media screen and (min-width: $small) {
      width: 400px;
      height: 400px;
    }
    @media screen and (max-width: $small) {
      width: 300px;
      height: 300px;
    }
  }
  h2 {
    font-size: 1.5rem;
  }
}
