@import url("https://fonts.googleapis.com/css2?family=Lora:wght@400;700&display=swap");
* {
  margin: 0;
  padding: 0;
  font-family: "Lora", serif;
  font-size: 1em;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

body {
  background-image: -webkit-gradient(linear, left bottom, left top, from(#dfe9f3), to(white));
  background-image: linear-gradient(to top, #dfe9f3 0%, white 100%);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  height: 99vh;
}

.menu {
  min-width: 100%;
  min-height: 3em;
  margin: 0;
  margin-top: 5%;
  background-image: url("https://cdn-icons-png.flaticon.com/512/56/56969.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 3em;
  -webkit-filter: opacity(0.4);
          filter: opacity(0.4);
}

@media screen and (min-width: 1000px) {
  .menu {
    min-height: 2em;
    margin-top: 0;
  }
}

@media screen and (max-width: 449px) {
  .menu {
    background-size: 2em;
    min-height: 2em;
  }
}

.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  min-height: 2em;
  margin-top: 4%;
  padding: 0.5%;
}

@media screen and (min-width: 1000px) {
  .header {
    min-height: 1em;
    width: 30%;
    margin: 1% auto;
  }
}

.header .link {
  text-decoration: none;
  color: black;
}

.header .link h2 {
  width: 100%;
  padding: 0;
  font-size: 1.5em;
}

@media screen and (min-width: 1000px) {
  .header .link h2 {
    font-size: 1em;
  }
}

@media screen and (max-width: 449px) {
  .header .link h2 {
    font-size: 1em;
  }
}

.header .link:visited {
  color: black;
}

.home-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.home-wrap .post-wrap {
  margin: 0 0 5% 0;
  padding: 5px;
  border-radius: 10px;
  -webkit-box-shadow: 2px 5px 15px rgba(0, 0, 0, 0.404);
          box-shadow: 2px 5px 15px rgba(0, 0, 0, 0.404);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: white;
}

@media screen and (max-width: 449px) {
  .home-wrap .post-wrap {
    width: 92%;
    font-size: 0.6em;
  }
}

@media screen and (min-width: 812px) {
  .home-wrap .post-wrap {
    width: 55%;
  }
}

@media screen and (min-width: 1000px) {
  .home-wrap .post-wrap {
    width: 29%;
    font-size: 0.7rem;
  }
}

.home-wrap .post-wrap img {
  max-width: 100%;
  border-radius: 5px;
}

.home-wrap .post-wrap h1 {
  font-size: 2em;
  margin-top: 2%;
  padding: 0 1%;
}

.home-wrap .post-wrap .text {
  font-size: 1.1em;
  margin: 2% 0;
  padding: 0 1%;
}

.hide {
  display: none;
}
