body {
  height: 99vh;
}

.about-wrap {
  background-color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 85vh;
  width: 55vw;
  margin: 0 auto;
  -webkit-box-shadow: 2px 5px 15px rgba(0, 0, 0, 0.404);
          box-shadow: 2px 5px 15px rgba(0, 0, 0, 0.404);
  border-radius: 10px;
}

@media screen and (max-width: 449px) {
  .about-wrap {
    width: 90vw;
    font-size: 0.75rem;
  }
}

@media screen and (min-width: 449px) {
  .about-wrap {
    width: 90vw;
  }
}

@media screen and (min-width: 760px) {
  .about-wrap {
    width: 90vw;
  }
}

@media screen and (min-width: 1000px) {
  .about-wrap {
    width: 50vw;
    height: 90vh;
  }
}

.about-wrap h1 {
  font-size: 2rem;
}

.about-wrap .image {
  background-image: url("https://sun9-85.userapi.com/impg/AnIREoZTEDkhEeDgFSelhJ0ibetybY1FzW7mTg/-coD18OZQ-U.jpg?size=1721x2160&quality=95&sign=907a9728ffab55f4dd39476a3e477120&type=album");
  background-size: cover;
  background-position: bottom;
  width: 50vw;
  height: 50vh;
  border-radius: 50%;
}

@media screen and (min-width: 760px) {
  .about-wrap .image {
    width: 550px;
    height: 550px;
  }
}

@media screen and (min-width: 449px) {
  .about-wrap .image {
    width: 400px;
    height: 400px;
  }
}

@media screen and (max-width: 449px) {
  .about-wrap .image {
    width: 300px;
    height: 300px;
  }
}

.about-wrap h2 {
  font-size: 1.5rem;
}
