@import url("https://fonts.googleapis.com/css2?family=Lora:wght@400;700&display=swap");
$small: 449px;
$medium: 812px;
$large: 1000px;

* {
  margin: 0;
  padding: 0;
  font-family: "Lora", serif;
  font-size: 1em;
  transition: all 0.5s ease-in-out;
}

body {
  background-image: linear-gradient(to top, #dfe9f3 0%, white 100%);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  height: 99vh;
}

.menu {
  min-width: 100%;
  min-height: 3em;
  margin: 0;
  margin-top: 5%;
  background-image: url("https://cdn-icons-png.flaticon.com/512/56/56969.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 3em;
  filter: opacity(0.4);
  @media screen and (min-width: $large) {
    min-height: 2em;
    margin-top: 0;
  }
  @media screen and (max-width: $small) {
    background-size: 2em;
    min-height: 2em;
  }
}
.header {
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  min-height: 2em;
  margin-top: 4%;
  padding: 0.5%;

  @media screen and (min-width: $large) {
    min-height: 1em;
    width: 30%;
    margin: 1% auto;
  }
  .link {
    text-decoration: none;
    color: black;
    h2 {
      width: 100%;
      padding: 0;
      font-size: 1.5em;
      @media screen and (min-width: $large) {
        font-size: 1em;
      }
      @media screen and (max-width: $small) {
        font-size: 1em;
      }
    }
  }
  .link:visited {
    color: black;
  }
}
.home-wrap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .post-wrap {
    margin: 0 0 5% 0;
    padding: 5px;
    border-radius: 10px;
    box-shadow: 2px 5px 15px rgba(0, 0, 0, 0.404);
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    @media screen and (max-width: $small) {
      width: 92%;
      font-size: 0.6em;
    }
    @media screen and (min-width: $medium) {
      width: 55%;
    }
    @media screen and (min-width: $large) {
      width: 29%;
      font-size: 0.7rem;
    }
    img {
      max-width: 100%;
      border-radius: 5px;
    }
    h1 {
      font-size: 2em;
      margin-top: 2%;
      padding: 0 1%;
    }
    .text {
      font-size: 1.1em;
      margin: 2% 0;
      padding: 0 1%;
    }
  }
}

.hide {
  display: none;
}
